<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isshow">
      <el-form :model="form">
        <el-form-item label="上级分类" label-width="120px">
          <el-select v-model="form.pid" placeholder="请选择活动区域">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option label="顶级菜单" :value="0"></el-option>
            <el-option v-for="item in list" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分类名称" label-width="120px">
          <el-input v-model="form.catename" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图片" label-width="120px" v-if="form.pid!='0'">
          <div class="my-upload">
            <h3>+</h3>
            <img :src="imgUrl" alt v-if="imgUrl" />
            <input type="file" class="ipt" @change="changeFile" />
          </div>
        </el-form-item>
         <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='商品分类添加'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { alertSuccess, alertWarning } from "../../../util/alert";
import { mapActions, mapGetters } from "vuex";
import {
  reqClassifyAdd,
  reqClassifyListOne,
  reqClassifyUpdate
} from "../../../util/request";
export default {
  props: ["info"],
  components: {},
  computed: {
    ...mapGetters({
      list: "classify/classify"
    })
  },
  data() {
    return {
      form: {
        pid: "",
        catename: "",
        img: null,
        status:1,
      },
      imgUrl: ""
    };
  },
  methods: {
    changeFile(e) {
      let file = e.target.files[0];

      if (file.size > 5 * 1024 * 1024) {
        alertWarning("文件太大了");
        return;
      }
      let imgArr = [".jpg", ".jpeg", ".png", ".gif"];
      let name = file.name;
      let imgName = name.slice(name.lastIndexOf("."));
      if (!imgArr.includes(imgName)) {
        alertWarning("文件格式不正确");
        return;
      }
      this.imgUrl = URL.createObjectURL(file);
      this.form.img = file;
    },
    cancel() {
      this.info.isshow = false;
    },
    empty() {
      this.form = {
        pid: "",
        catename: "",
        img: null,
        status: 1
      };
      this.imgUrl = "";
    },
    add() {
      let data = new FormData();
      //   data.append('pid',this.form.pid);
      //   data.append('catename',this.form.catename);
      //   data.append('img',this.form.img);
      //   data.append('status',this.form.status);
      for (let i in this.form) {
        data.append(i, this.form[i]);
      }
      reqClassifyAdd(data).then(res => {
        if (res.data.code == 200) {
          alertSuccess(res.data.msg);
          this.empty();
          this.cancel();
          this.requsetClassifyList();
        }
      });
    },
    getOne(id) {
      reqClassifyListOne(id).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.list;
          this.form.id = id;

          this.imgUrl = this.$imgPre + this.form.img;
        }
      });
    },
    update() {
      let data = new FormData();
      for (let i in this.form) {
        data.append(i, this.form[i]);
      }
      reqClassifyUpdate(data).then(res => {
        if (res.data.code === 200) {
          alertSuccess(res.data.msg);
          this.cancel();
          this.empty();
          this.requsetClassifyList();
        }
      });
    },
    ...mapActions({
      requsetClassifyList: "classify/requsetClassifyList"
    })
  },
  mounted() {
    this.requsetClassifyList();
  }
};
</script>
<style scoped>
.my-upload {
  width: 150px;
  height: 150px;
  border: 1px dashed #ccc;

  position: relative;
}
.my-upload h3 {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 150px;
  color: #666;
  font-size: 30px;
}
.my-upload .ipt {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 2;
  cursor: pointer;
}
.my-upload img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
</style>


