<template>
  <div>
    <el-table
      :data="list"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
    >
      <el-table-column prop="id" label="分类编号"></el-table-column>
      <el-table-column prop="catename" label="分类名称"></el-table-column>
      <el-table-column label="图片">
        <template slot-scope="scope">
          <img :src="$imgPre+scope.row.img" alt v-if="scope.row.img!=='null'" />
        </template>
      </el-table-column>

      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status===1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删 除</el-button>
          <!-- <del-btn @confirm="del(scope.row.id)"></del-btn> -->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { alertSuccess, alertWarning } from "../../../util/alert";
import { mapActions, mapGetters } from "vuex";
import { reqClassifyDel } from "../../../util/request";
export default {
  components: {},
  computed: {
    ...mapGetters({
      list: "classify/classify"
    })
  },
  data() {
    return {};
  },
  methods: {
    ...mapActions({
      requsetClassifyList: "classify/requsetClassifyList"
    }),
    del(id) {
      this.$confirm("你确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          reqClassifyDel(id).then(res => {
            if (res.data.code === 200) {
              alertSuccess(res.data.msg);
              this.requsetClassifyList();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    edit(id) {
        this.$emit('edit',id)
    },
    
  },
  mounted() {
    this.requsetClassifyList();
  }
};
</script>
<style scoped>
img{
    width: 100px;
    height: 100px;
}
</style>


